"use client";

import * as React from 'react';
import Box from '@mui/material/Box';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import ImageModal from './ImageModal';

export default function FloatingActionButton({ onSave }) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  // When the modal's save is triggered, call the parent's onSave and close the modal
  const handleSave = (newImage) => {
    onSave(newImage);
    setOpen(false);
  };

  return (
    <>
      <Box sx={{ '& > :not(style)': { m: 1 } }}>
        <Fab aria-label="add" sx={{ backgroundColor: "tan" }} onClick={handleOpen}>
          <AddIcon />
        </Fab>
      </Box>
      <ImageModal
        open={open}
        mode="upload"
        onClose={handleClose}
        onSave={handleSave}
      />
    </>
  );
}